/* StructuredWorkflow.css */
.workflow-section {
  padding: clamp(80px, 12vh, 120px) 0;
  background: linear-gradient(135deg, #2c2c2c 0%, #383737 50%, #2c2c2c 100%);
  color: #fff;
  text-align: center;
  position: relative;
  overflow: hidden;
  font-family: 'Poppins', sans-serif;
}

.workflow-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 50%, rgba(230, 92, 0, 0.05) 0%, transparent 70%);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

.title-container {
  margin-bottom: 60px;
  position: relative;
}

.title-container::after {
  content: '';
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: #e65c00;
  border-radius: 3px;
}

.sub-title {
  font-size: 1.25rem;
  color: #e65c00;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 10px;
  font-weight: 600;
}

.main-title {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  color: #ffffff;
  margin: 0;
  line-height: 1.2;
}

.cards-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  justify-content: center;
  
  @media (max-width: 900px) {
    gap: 20px;
  }
  
  @media (max-width: 600px) {
    grid-template-columns: 1fr;
    max-width: 400px;
    margin: 0 auto;
  }
}

.card {
  background: linear-gradient(145deg, #4d4c4c 0%, #3a3939 100%);
  border-radius: 20px;
  padding: 40px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.4s ease;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  height: 100%;
  opacity: 0;
  transform: translateY(30px);
}

.card.animate {
  animation: fadeInUp 0.8s forwards;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 0;
  background: #e65c00;
  transition: height 0.4s ease;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}

.card:hover::before {
  height: 100%;
}

.icon-container {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: rgba(230, 92, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  transition: all 0.3s ease;
}

.card:hover .icon-container {
  background: rgba(230, 92, 0, 0.2);
  transform: scale(1.1);
}

.icon {
  font-size: 2.2rem;
  color: #e65c00;
}

.number {
  font-size: 3.5rem;
  color: #e65c00;
  font-weight: 700;
  margin: 10px 0;
  position: relative;
}

.number::after {
  content: '+';
  font-size: 2rem;
  position: absolute;
  top: 5px;
  margin-left: 5px;
}

.card-title {
  font-size: 1.4rem;
  margin: 15px 0;
  font-weight: 600;
  color: #ffffff;
}

.card-description {
  font-size: 1rem;
  color: #cccccc;
  line-height: 1.6;
  margin-top: auto;
}

/* TrustedBy.css */
.trusted-by-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 40px;
  background-color: #ffffff;
  font-family: 'Poppins', sans-serif;
  opacity: 0;
  transition: opacity 1s ease;
}

.trusted-by-container.fade-in {
  opacity: 1;
}

.trusted-by-title {
  font-size: clamp(2rem, 4vw, 3rem);
  color: #333;
  margin-bottom: 40px;
  font-weight: 600;
  text-align: center;
  position: relative;
}

.trusted-by-title::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: #e65c00;
  border-radius: 3px;
}

.highlight {
  color: #e65c00;
  font-weight: bold;
}

.carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  margin-top: 30px;
}

.logos {
  display: flex;
  gap: 40px;
  overflow-x: auto;
  scroll-behavior: smooth;
  width: 80%;
  padding: 20px 0;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.logos::-webkit-scrollbar {
  display: none;
}

.customer-logo {
  height: 60px;
  width: auto;
  filter: grayscale(100%);
  opacity: 0.7;
  transition: filter 0.4s ease, transform 0.4s ease, opacity 0.4s ease;
}

.customer-logo.visible {
  animation: fadeIn 0.8s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 0.7;
    transform: translateY(0);
  }
}

.customer-logo:hover {
  filter: grayscale(0%);
  transform: translateY(-5px);
  opacity: 1;
}

.arrow {
  background-color: #fff;
  border: 1px solid #e65c00;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 15px;
  cursor: pointer;
  font-size: 1.2rem;
  color: #070707;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.arrow:hover {
  background-color: #e65c00;
  color: white;
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .trusted-by-container {
    padding: 40px 20px;
  }
  
  .logos {
    width: 70%;
    gap: 30px;
  }
  
  .customer-logo {
    height: 50px;
  }
}

@media (max-width: 480px) {
  .logos {
    width: 60%;
    gap: 20px;
  }
  
  .customer-logo {
    height: 40px;
  }
  
  .arrow {
    width: 35px;
    height: 35px;
    font-size: 1rem;
  }
}